<template>
    <div class="filter-vacancy">
        <div class="d-flex mb-5 align-items-center">
            <el-input placeholder="   " v-model="filters.vacancyField" @keyup.enter.native="clientFilter" clearable @clear="clientFilter">
                <i slot="prefix" class="el-input__icon el-icon-search"></i>
                <template slot="append">
                    <el-button type="primary" @click="clientFilter"></el-button>
                </template>
            </el-input>
        </div>
    </div>
</template>

<script>
    import {VACANCIES} from "../../store/types/vacancies";
    import {SET_FILTERS} from "../../store/mutation-types";
    export default {
        name: 'filter-vacancy',
        data() {
            return {
                filters: {
                    vacancyField: '',
                    type: [],
                    minSalary: 0,
                    maxSalary: 300000
                },
                vacancyFields: {
                    value:''
                },
                city: {
                    options: [{
                        value: '',
                        label: ''
                    }, {
                        value: '',
                        label: ''
                    }, {
                        value: '',
                        label: ''
                    }, {
                        value: '',
                        label: ''
                    }, {
                        value: '',
                        label: ''
                    }, {
                        value: '',
                        label: ''
                    }, {
                        value: '',
                        label: ''
                    }],
                    value: ''
                },
                employment: {
                    options: [
                        {label: ' ', value: ' '},
                        {label: ' ', value: ' '},
                        {label: ' ', value: ' '},
                        {label: '', value: ''},
                        {label: '', value: ''}
                    ],
                    values: []
                },
                search: {
                    value: ''
                },
            }
        },
        methods: {
            onFilter() {
                const filters = {
                    minSalary: this.filters.minSalary,
                    maxSalary: this.filters.maxSalary,
                }
                if (this.filters.vacancyField !== '') {
                    filters.vacancyField = this.filters.vacancyField
                }
                if (this.filters.type.length > 0) {
                    filters.type = this.filters.type
                }
                this.$emit('filter', filters)
            },
            clientFilter() {
                this.$store.commit(VACANCIES + SET_FILTERS, this.filters.vacancyField)
            }
        }
    }
</script>